#nav-icon1 {
  width: 45px;
  height: 35px;
  position: relative;
  -webkit-transform: rotate(0deg);
  transform: rotate(0deg);
  transition: 0.5s ease-in-out;
  cursor: pointer;
  z-index: 10;
  float: right;
  margin: 10px auto;
}

#nav-icon1 {
}

#nav-icon1 span {
  display: block;
  position: absolute;
  height: 5px;
  width: 50%;
  background-color: #0a1684;
  opacity: 1;
  -webkit-transform: rotate(0deg);
  transform: rotate(0deg);
  transition: 0.25s ease-in-out;
}

#nav-icon1 span:nth-child(even) {
  left: 50%;
  border-radius: 0 9px 9px 0;
}

#nav-icon1 span:nth-child(odd) {
  left: 0px;
  border-radius: 9px 0 0 9px;
}

#nav-icon1 span:nth-child(1),
#nav-icon1 span:nth-child(2) {
  top: 0px;
}

#nav-icon1 span:nth-child(3),
#nav-icon1 span:nth-child(4) {
  top: 10px;
}

#nav-icon1 span:nth-child(5),
#nav-icon1 span:nth-child(6) {
  top: 20px;
}

#nav-icon1.open span:nth-child(1),
#nav-icon1.open span:nth-child(6) {
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
}

#nav-icon1.open span:nth-child(2),
#nav-icon1.open span:nth-child(5) {
  -webkit-transform: rotate(-45deg);
  transform: rotate(-45deg);
}

#nav-icon1.open span:nth-child(1) {
  left: 5px;
  top: 12px;
  background-color: white;
}

#nav-icon1.open span:nth-child(2) {
  left: calc(50% - 5px);
  top: 12px;
  background-color: white;
}

#nav-icon1.open span:nth-child(3) {
  left: -50%;
  opacity: 0;
  background-color: white;
}

#nav-icon1.open span:nth-child(4) {
  /* left: 100%; */
  opacity: 0;
  background-color: white;
}

#nav-icon1.open span:nth-child(5) {
  left: 5px;
  top: 25px;
  background-color: white;
}

#nav-icon1.open span:nth-child(6) {
  left: calc(50% - 5px);
  top: 25px;
  background-color: white;
}

