.card {
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 0.1;
  z-index: 0;
  overflow: hidden;
}

.card.active {
  opacity: 1;
  -webkit-animation: fronte 0.3s linear;
          animation: fronte 0.3s linear;
  z-index: 1;
  transition: all 1s cubic-bezier(0, 0.77, 0.2, -0.23);
  -webkit-animation: display 0.2s linear;
          animation: display 0.2s linear;
}

.card img {
  height: 100%;
  width: 100%;
}

@-webkit-keyframes display {
  0% {
    z-index: 0;
    opacity: 0.5;
  }
  25% {
    z-index: 0.3;
    opacity: 0.6;
  }
  50% {
    z-index: 0.5;
    opacity: 0.7;
  }
  75% {
    z-index: 0.7;
    opacity: 0.8;
  }
  85% {
    z-index: 0.7;
    opacity: 0.9;
  }
  100% {
    z-index: 1;
    opacity: 1;
  }
}

@keyframes display {
  0% {
    z-index: 0;
    opacity: 0.5;
  }
  25% {
    z-index: 0.3;
    opacity: 0.6;
  }
  50% {
    z-index: 0.5;
    opacity: 0.7;
  }
  75% {
    z-index: 0.7;
    opacity: 0.8;
  }
  85% {
    z-index: 0.7;
    opacity: 0.9;
  }
  100% {
    z-index: 1;
    opacity: 1;
  }
}

@-webkit-keyframes fronte {
  from {
    z-index: 0.6;
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
    opacity: 0.5;
  }
  to {
    -webkit-transform: scale(1);
            transform: scale(1);
    z-index: 1;
    opacity: 1;
  }
}

@keyframes fronte {
  from {
    z-index: 0.6;
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
    opacity: 0.5;
  }
  to {
